import React, { useState, useEffect } from 'react';
import Navbar from '../Navbar';
import MusicPage from '../MusicReleases/MusicPage';
import Chat from './Chat';
import MasteringPage from './MasteringPage';
import './HomePage.css';

const HomePage = () => {
  const [active, setActive] = useState(false);
  const [showContent, setShowContent] = useState(false);

  const handleMouseOver = () => {
    setActive(true);
  };

  const scrollToSection = (sectionId) => {
    const section = document.getElementById(sectionId);
    if (section) {
      window.scrollTo({
        top: section.offsetTop,
        behavior: 'smooth',
      });
    }
  };

  useEffect(() => {
    setShowContent(true);

    const handleScroll = () => {
      const chatSection = document.getElementById('chat');
      const chatSectionBottom = chatSection.offsetTop + chatSection.offsetHeight;
      const windowBottom = window.pageYOffset + window.innerHeight;

      if (windowBottom >= chatSectionBottom) {
        chatSection.classList.add('visible');
      } else {
        chatSection.classList.remove('visible');
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <div>
      <Navbar />
      <div className={`home-content ${showContent ? 'show' : ''}`} onClick={() => scrollToSection('chat')}>
        <h1>
          <span onMouseOver={handleMouseOver} className={active ? 'image-active' : ''}>
            BLOXE
          </span> NOT BLOKE
        </h1>
      </div>
      <MusicPage />
      <MasteringPage />
      <Chat id="chat" />
    </div>
  );
};

export default HomePage;
