import React, { useState, useEffect } from 'react';
import './App.css';
import PasswordPage from './PasswordPage';
import HomePage from './Home/HomePage';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import GradientBackground from './Home/Background';
import ReactGA from 'react-ga';

const App = () => {
  // Initialize Google Analytics
  ReactGA.initialize('G-8WQS67NW1J');
  ReactGA.pageview(window.location.pathname + window.location.search);

  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const colorStops = ['#efe7e4', '#D3D3D3', '#A9A9A9', '#6D6875'];

  useEffect(() => {
    // Clear authentication status when re-entering the site
    localStorage.removeItem('isAuthenticated');

    // Check and set authentication status
    const authStatus = localStorage.getItem('isAuthenticated') === 'true';
    setIsAuthenticated(authStatus);
  }, []);

  const handlePasswordSuccess = () => {
    localStorage.setItem('isAuthenticated', 'true');
    setIsAuthenticated(true);

    // Track pageview for homepage when password is successfully entered
    ReactGA.pageview('/home');
  };

  return (
    <div className="App">
      <BrowserRouter>
        {isAuthenticated ? (
          <GradientBackground colorStops={colorStops}>
            <AuthenticatedRoutes />
          </GradientBackground>
        ) : (
          <UnauthenticatedRoutes onPasswordSuccess={handlePasswordSuccess} />
        )}
      </BrowserRouter>
    </div>
  );
};

const AuthenticatedRoutes = () => (
  <Routes>
    <Route path="/home" element={<HomePage />} />
    <Route path="*" element={<HomePage />} />
  </Routes>
);

const UnauthenticatedRoutes = ({ onPasswordSuccess }) => (
  <Routes>
    <Route path="/" element={<PasswordPage onPasswordSuccess={onPasswordSuccess} />} />
    <Route path="*" element={<PasswordPage onPasswordSuccess={onPasswordSuccess} />} />
  </Routes>
);

export default App;
