
import React, { useState, useEffect } from 'react';
import './PasswordPage.css';
import image1 from './images/image1.png';
import image2 from './images/image2.png';
import image3 from './images/image3.png';
import image4 from './images/image4.png';

const PasswordPage = ({ onPasswordSuccess }) => {
  const [hidePasswordPage, setHidePasswordPage] = useState(false);
  const [hoveredImage, setHoveredImage] = useState(null);
  const [clickedImages, setClickedImages] = useState([]);

  useEffect(() => {
    const animateSequence = async () => {
      // Initial delay before the animation starts
      await new Promise((resolve) => setTimeout(resolve, 2000)); // Adjust the delay as needed (changed from 1000)

      const images = [1, 2, 3, 4];

      for (let i = 0; i < images.length; i++) {
        // Simulate a hover effect
        setHoveredImage(images[i]);
        await new Promise((resolve) => setTimeout(resolve, 200)); // Adjust the delay as needed (was 500)

        // Simulate a click
        setClickedImages((prevClickedImages) => [...prevClickedImages, images[i]]);
        await new Promise((resolve) => setTimeout(resolve, 200)); // Adjust the delay as needed (was 500)

        // Simulate a shrink animation for the clicked image
        await new Promise((resolve) => setTimeout(resolve, 200)); // Adjust the delay as needed (added this line)
      }

      // Automatically unlock after the sequence is completed
      setHidePasswordPage(true);
      onPasswordSuccess();
    };

    // Start the sequence when the component mounts
    animateSequence();
  }, [onPasswordSuccess]);

  return (
    <div className={`password-page ${hidePasswordPage ? 'hidden' : ''}`}>
      <div className="images-container animate">
        {[image1, image2, image3, image4].map((img, index) => (
          <img
            key={index}
            src={img}
            alt={`${index + 1}`}
            className={`password-image ${hoveredImage === index + 1 ? 'hovered' : ''} ${clickedImages.includes(index + 1) ? 'clicked' : ''}`}
          />
        ))}
      </div>
    </div>
  );
};

export default PasswordPage;
